.icon-phone:before {
  content: ""; }

.icon-gmail:before {
  content: ""; }

.icon-telegram:before {
  content: ""; }

.icon-vk:before {
  content: ""; }

[class^="icon-"] {
  margin: 0em .1em;
  padding: .3em .3em;
  display: inline-block;
  vertical-align: middle; }
  [class^="icon-"]:before {
    font-family: icomoon;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }

.icon-vk {
  color: #4C75A3; }

.icon-gmail {
  color: #E04C40; }

.icon-telegram {
  color: #2CA5E0; }

@font-face {
  font-family: "icomoon";
  src: url("../../assets/fonts/icomoon/icomoon.eot?") format("eot"), url("../../assets/fonts/icomoon/icomoon.ttf") format("truetype"), url("../../assets/fonts/icomoon/icomoon.woff") format("woff"); }

/* layout */
.flexbox {
  display: flex; }
  @media (max-width: 640px) {
    .flexbox {
      flex-direction: column; } }

.flex-items-center {
  align-items: center; }

.cv__container {
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  max-width: 940px;
  font-family: "Open Sans", sans-serif; }
  .cv__container* {
    box-sizing: border-box; }

.cv__content-block {
  padding: 20px 40px; }

.cv__row-wrapper {
  margin-right: -15px;
  margin-left: -15px;
  padding-bottom: 20px;
  display: flex;
  align-items: center; }
  @media (max-width: 640px) {
    .cv__row-wrapper {
      flex-direction: column; } }

.cv__col {
  padding-right: 15px;
  padding-left: 15px; }

.cv-col-title {
  position: relative;
  flex: 2;
  min-width: 220px;
  font-weight: 400;
  font-size: 15px; }

.cv-col-content {
  position: relative;
  flex: 5;
  font-weight: 500;
  font-size: 14px; }

.cv-section-title {
  margin-right: 30px;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  white-space: nowrap;
  color: #099778; }

.cv__delimiter {
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.39); }
  @media (max-width: 640px) {
    .cv__delimiter {
      display: none; } }

/* header section */
.cv-section-header__author-name {
  color: #099778;
  font-size: 30px; }

.cv-section-header__author-photo {
  border-radius: 1000px;
  margin-left: auto;
  margin-right: auto;
  width: 100px;
  height: 100px;
  background: url("../../../assets/img/avatar.jpg") no-repeat;
  background-size: 195px;
  background-position: -68px center; }

.cv-contact__socials-list {
  padding-left: 0;
  font-size: 11px;
  font-weight: 500; }

.cv-contact__socials-item {
  list-style: none;
  border-bottom: 1px solid rgba(9, 151, 120, 0.43);
  margin-bottom: 5px; }
  .cv-contact__socials-item [class^="icon-"] {
    margin-right: 5px; }
    .cv-contact__socials-item [class^="icon-"]:before {
      font-size: 1.4em; }
  .cv-contact__socials-item a {
    color: rgba(0, 0, 0, 0.71);
    text-decoration: none; }

/* portfolio */
.cv-portfolio-item__slides-list {
  padding-left: 0;
  height: 300px; }

.cv-portfolio-item__slides-item img {
  max-width: 100%; }

.cv-portfolio__page-list {
  padding-left: 0; }

.cv-portfolio__page-item {
  border-bottom: 1px solid rgba(9, 151, 120, 0.6);
  margin-bottom: 5px;
  padding-top: 3px;
  padding-bottom: 3px;
  list-style: none; }
  .cv-portfolio__page-item.active a {
    color: #099778; }
  .cv-portfolio__page-item a {
    text-decoration: none;
    color: #000;
    font-size: 11px;
    font-weight: 500; }
